import request from '../utils/request';

const add = data =>
  request({
    url: '/app/moments/add',
    method: 'post',
    data,
  });

const list = page =>
  request({
    url: '/app/moments/list',
    method: 'get',
    params: {
      page,
    },
  });

export default {
  add,
  list,
};



