<template>
  <ul class="grid grid--type-a" v-infinite-scroll="loadMore" infinite-scroll-disabled="loading" infinite-scroll-distance="10" style="height: 300px">
    <div class="grid__sizer"></div>
    <li class="grid__item" v-for="(item, i) in list" :key="i">
      <a class="grid__link">
        <img class="grid__img" :src="item.cover_image" alt="">
        <p>{{item.description}}</p>
        <div class="footer">
          <div class="left">
          <img :src="item.headimgurl" alt="" class="head">
          <span>{{item.nickname}}</span>
          </div>
          <div class="right">
            <img src="../assets/like.svg" alt="" class="heart">
            {{item.upvote_number}}
          </div>
        </div>
      </a>
    </li>
  </ul>
</template>
<script>
import imagesloaded from 'imagesloaded';
import Masonry from 'masonry-layout';

export default {
  name: 'people-list',
  props: {
    list: {
      type: Object,
    },
    loadMore: {
      type: Function,
    },
  },
  data() {
    return {
      list: ['avatar.png', 'close.svg', 'fire.png'],
      top: [],
    };
  },
  mounted() {
    imagesloaded('.grid', () => {
      const msnry = new Masonry('.grid', {
        itemSelector: '.grid__item',
        columnWidth: '.grid__sizer',
        percentPosition: true,
        transitionDuration: 0,
      });
      // grids.forEach(grid => {
      //   const m = new Masonry(grid, {
      //     itemSelector: '.grid__item',
      //     columnWidth: '.grid__sizer',
      //     percentPosition: true,
      //     transitionDuration: 0,
      //   });
      //   masonry.push(m);
      //   // Hide the grid.
      //   grid.classList.add('grid--hidden');
      //   // Init GridLoaderFx.
      // });
      // grids[currentGrid].classList.remove('grid--hidden');
      // Init/Bind events.
      // Remove loading class from body
    });
    // console.log(masonry);
  },
};
</script>
<style lang="scss" scoped>
.grid {
  position: relative;
  z-index: 0;
  display: block;
  margin: 7px auto 0;
}
.grid .grid__item,
.grid .grid__sizer {
  position: absolute;
  width: calc(50% - 20px);
  margin: 0 10px 20px;
}

.grid__link,
.grid__img {
  display: block;
}

.grid__img {
  width: 100%;
}
.grid__img {
  width: 100%;
}

.footer {
  width: 100%;
  height: 36px;
  display: flex;
  justify-content: space-between;
  img {
    width: 30px;
    height: 30px;
    border-radius: 50%;
  }
  span {
    font-weight: bold;
    font-size: 13px;
    color: #333;
  }
  img.heart {
    width: 15px;
  }
}
</style>





